.vous_etes {
    // height: 185px;
    background: $primary_light_grey;
    @media screen and (min-width: 1024px) and (max-width: 1024px) {
        #titre {
            margin: auto 0 !important;
            font-family: $main_font;
            font-size: 1.25em !important;
            letter-spacing: 3px;
            color: $primary_dark_grey;
            p {
                line-height: auto !important;
                &::after {
                    content: url(../img/arrow_who.png);
                    margin-left: 1% !important;
                }
            }
        }
    }
    @media screen and (min-width: 768px) and (max-width: 1024px) {
        #admin h4,
        #syndic h4 {
            position: relative !important;
            left: 0 !important;
            right: 0 !important;
        }
        img {
            margin: 5% 0 !important;
            width: auto !important;
        }
        #contact {
            button {
                padding: 2% 7% !important;
                margin: 3%0 !important;
            }
        }
    }
    @media screen and (max-width: 767px) {
        h4 {
            position: relative !important;
            right: unset !important;
            left: unset !important;
            bottom: unset !important;
        }
        img {
            width: auto !important;
            margin: 10% 0 5% 0 !important;
        }
        #titre {
            p {
                margin: 5% 0 0 0 !important;
            }
        }
        p {
            line-height: unset !important;
            font-size: 1.041875em !important;
            &::after {
                display: none !important;
            }
        }
        div {
            padding: 0 !important;
        }
        button {
            margin: 10% 0 !important;
        }
    }
    img {
        // width: 50%;
        color: #3a4953;
        margin: 11% 0;
        width: 55%;
    }
    #titre {
        font-family: $main_font;
        font-size: 1.56em;
        letter-spacing: 3px;
        color: $primary_dark_grey;
        p {
            @media screen and (min-width: 1199px) {
                line-height: 200px;
            }
            &::after {
                content: url(../img/arrow_who.png);
                margin-left: 5%;
            }
        }
    }
    #contact {
        button {
            font-family: $main_font;
            text-transform: uppercase;
            letter-spacing: 5px;
            border: none;
            background-color: #3a4853;
            padding: 3% 5%;
            border-radius: 30px;
            // margin-top: 20%;
            transition: 0.3s;
            &:hover {
                filter: brightness(1.2);
            }
            a {
                color: #ffffff !important;
                font-weight: 200;
                font-size: 1em;
                &:hover {
                    text-decoration: none !important;
                }
            }
        }
    }
    #admin {
        padding-right: 5%;
        h4 {
            font-family: $main_font;
            text-transform: uppercase;
            letter-spacing: 3px;
            font-size: 1em;
            color: #3a4953;
            position: absolute;
            right: 20%;
            bottom: 11%;
        }
    }
    #syndic {
        padding-left: 5%;
        h4 {
            font-family: $main_font;
            text-transform: uppercase;
            letter-spacing: 3px;
            font-size: 1em;
            color: #3a4953;
            position: absolute;
            left: 20%;
            bottom: 11%;
        }
    }
}

.garantie {
    @media screen and (min-width: 1024px) and (max-width: 1024px) {
        .big_pres {
            img {
                position: absolute !important;
                top: 25% !important;
                right: 15% !important;
                width: 78% !important;
            }
        }
    }
    @media screen and (min-width: 768px) and (max-width: 992px) {
        .title {
            padding: 10% 0 !important;
            hr {
                width: 40% !important;
                float: none !important;
                margin-left: 0 !important;
            }
        }
        .big_pres {
            max-width: 96% !important;
            margin-left: 2%;
            img {
                right: 31% !important;
            }
        }
        .garantie_item_container {
            margin: 2% 0 !important;
            h4 {
                top: 65% !important;
            }
            img {
                top: 50% !important;
            }
        }
    }
    @media screen and (max-width: 767px) {
        h1 {
            font-size: 2.34375em !important;
        }
        h5 {
            font-size: 1.041875em !important;
        }
        .title {
            padding: 10% 0 !important;
            hr {
                width: 40% !important;
                float: none !important;
                margin-left: 0 !important;
            }
        }
        .big_pres {
            max-width: 92% !important;
            img {
                top: 15% !important;
                right: 20% !important;
            }
            margin: 0 auto !important;
        }
        .garantie_item_container {
            margin-bottom: 0 !important;
            margin-top: 5% !important;
            h4 {
                transform: translate(-80%, -35%) !important;
            }
            img {
                top: 50% !important;
            }
        }
    }
    .title {
        padding: 5% 0 !important;
        h1 {
            font-family: $main_font;
            font-size: 2.8125em;
            font-weight: 400;
            color: #3d4c57;
            margin-bottom: 0 !important;
            span {
                color: $primary_blue;
            }
        }
        h5 {
            font-family: $main_font;
            font-weight: 300;
            text-transform: uppercase;
            letter-spacing: 2px;
            color: #3d4c57;
            margin: 25px 0 30px 0;
        }
        hr {
            width: 6%;
            float: left;
            border-top: 5px solid rgb(61, 76, 87);
            // margin: 0;
        }
    }
    .big_pres {
        background-color: $primary_light_grey;
        height: 290px;
        img {
            position: absolute;
            top: 15%;
            right: 15%;
        }
    }
    .garantie_item_container {
        background-color: $primary_light_grey;
        border-left: 20px solid $primary_blue;
        height: 125px;
        margin-bottom: 40px;
        transition: .3s;
        h4 {
            font-family: $main_font;
            font-weight: 200;
            color: #3d4c57;
            width: 45%;
            line-height: 30px;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-80%, -80%);
        }
        img {
            position: absolute;
            right: 10%;
            top: 35%;
        }
        &:hover {
            background: $primary_blue;
            h4 {
                color: #ffffff;
            }
        }
    }
}

