            padding: 3% 5% !important;
            margin: 5% 0 10% 0 !important;
        }
    }
    @media screen and (min-width: 768px) and (max-width: 992px) {
        .content_actualite {
            .arrow {
                position: absolute !important;
                width: 20% !important;
                right: 11% !important;
                top: 61% !important;
            }
            .title_actu {
                font-size: 0.875em !important;
            }
        }
    }
    button {
        padding: 0.8% 4.5% !important;
        margin: 6% 0 !important;
    }
    .title {
