        }
        h5 {
            font-family: $main_font;
            font-weight: 300;
            text-transform: uppercase;
            letter-spacing: 2px;
            color: #3d4c57;
            margin: 20px 0 25px 0;
        }
        hr {
            width: 6%;
            border-top: 5px solid rgb(61, 76, 87);
            float: left;
            margin: 0;
        }
    }
    .actualite {
        a {
            &:hover {
                text-decoration: none;
                ;
            }
        }
    }
    .content_actualite {
        background: $primary_light_grey;
        padding: 5%;
        img {
            transition: 0.3s;
        }
        .arrow {
            float: right;
            padding-top: 3%;
        }
        .date {
            font-family: $main_font;
            text-transform: uppercase;
            font-weight: 200;
            font-size: 0.91125em;
