.container-fluid {
    padding-left: 0 !important;
    padding-right: 0 !important;
}

@media screen and (max-width: 1024px) {
    body {
        overflow-x: hidden;
    }
    .mx-15 {
        width: 90% !important;
        margin: 0 auto !important;
    }
}

.cover {
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

section {
    position: relative;
    overflow-x: hidden !important;
    .border_dark_grey {
        position: absolute;
        top: 0;
        right: 0;
        background-color: $second_dark_grey;
        height: 100%;
        width: calc(100% / 16);
    }
    .border_grey {
        position: absolute;
        top: 0;
        right: 0;
        background-color: $primary_dark_grey;
        height: 100%;
        width: calc(100% / 16);
    }
}

.pic_rounded_container {
    @extend .cover;
    height: 300px;
    width: 300px;
    margin: 0 auto;
    background-image: url('http://localhost/aiac_2/wp-content/themes/Aiac-immeubles/img/border_cercle.png');
    img {
        width: 90%;
        height: 90%;
        border-radius: 50%;
        margin-top: 5%;
    }
    @media screen and (min-width: 768px) and (max-width: 1024px) {
        height: 200px;
        width: 200px;
        margin-bottom: 10%;
    }
}